@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap');

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

::-webkit-scrollbar {
	width: 10px;
	height: 5px;
}

::-webkit-scrollbar-thumb {
	background: #525252;
	border-radius: 7px;
}

::-webkit-scrollbar-thumb:hover {
	background: #616161;
}

li { list-style-type: none; }

a {
	text-decoration: none;
	color: #7c73e6;
}

html {
	scroll-behavior: smooth;
}

#root {
	min-height: 100vh;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	flex-direction: column;
}

body {
	font-size: 12px; 
    font-family: 'Source Sans Pro', sans-serif;
	background-color: #363434;
	min-width: 250px;
}

h2 {
	font-size: 22px;
}

h3 {
	font-size: 17px;
}

h4 {
	font-size: 15px;
}

main {
	margin-bottom: 40px;
	width: 100%;
}

#progress-bar {
	display: none;
	position: fixed;
	top: 60px;
	left: 0;
	height: 5px;
	width: 20%;
	background-color: #2b6adf;
	z-index: 100;
	border-radius: 100px;
}

@keyframes progress-bar {
	from { margin-left: -20%; }
	to { margin-left: calc(100%); }
}

.img-cont {
	display: block;
	position: relative;
}

.img-cont img { display: block; }

.img-cont .img-cover {
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
}

nav {
	font-size: 15px;
}

.page-nav {
	width: 100%;
	max-width: 1100px;
	margin: 0 auto 20px auto;
}

.page-nav button {
	padding: 7px 10px;
	border-radius: 10px;
	margin-right: 10px;
	background-color: #7c73e6;
	color: #eeeeee;
	transition: background-color 100ms ease;
}

.page-nav button:hover { background-color: #9389ff; }
	
.alt-bg:nth-of-type(even) { background-color: #3f3e3e; }

button, textarea, input, select {
	border: none;
	outline: none;
}

.table-items, .box-items {
	height: 100%;
	width: 100%;
	max-width: 1100px;
	margin: 0 auto;
}

.no-result-container {
	height: 100%;
	width: 100%;
	max-width: 1100px;
	margin: 0 auto;
	color: #eee;
}

.no-result-container img {
	text-align: left;
	margin: 0 auto;
	width: 60%;
	max-width: 500px;
}

.no-result-container ul {
	margin-top: 10px;
}

@keyframes shake {
	0% {
		transform: translate(0, 0);
	}
	10% {
		transform: translate(2px, -4px);
	}
	20% {
		transform: translate(-3px, 0);
	}
	30% {
		transform: translate(-5px, 2px);
	}
	40% {
		transform: translate(-7px, -4px);
	}
	50% {
		transform: translate(-3px, 2px);
	}
	60% {
		transform: translate(5px, 6px);
	}
	70% {
		transform: translate(8px, 8px);
	}
	80% {
		transform: translate(3px, 2px);
	}
	90% {
		transform: translate(-2px, -3px);
	}
	100% {
		transform: translate(0, 0);
	}
}